import React, { useContext } from 'react'
import Container from '../../../ui/container/container'
// import Logo from '../../../ui/logo/logo'
import {
  Programmer,
  Sleeping,
  Github,
  LinkedIn,
  Dribble,
  University,
  Diner,
  Working,
  Tea,
  WatchingMovie,
} from '../../../ui/svg/svg'
import ReactTypingEffect from 'react-typing-effect'
import SCIntro from '../../scene/intro/intro'
import UMath from '../../../utils/umath'
import ClockContext from '../../../context/clockContext'
import './intro.css'

const Intro = () => {
  const birthDay = new Date(2001, 8, 19)
  const today = new Date()
  const startDate = new Date(2018, 8, 7)
  const age = UMath.diffYears(birthDay, today)
  const years = UMath.diffYears(startDate, today)
  const speed = 100
  const eraseDelay = 5000
  const typingDelay = 1
  const clock = useContext(ClockContext)
  let emoji = <Sleeping />
  let state = 'Sleeping'
  const hour = clock.time[0]
  switch (true) {
    case 1 <= hour && hour <= 7:
      emoji = <Sleeping />
      state = 'Sleeping'
      break
    case 7 <= hour && hour <= 14:
      emoji = <University />
      state = 'At University'
      break
    case 14 <= hour && hour <= 15:
      emoji = <Diner />
      state = 'Having Diner'
      break
    case 15 <= hour && hour <= 17:
      emoji = <Working />
      state = 'Working'
      break
    case 17 <= hour && hour <= 18:
      emoji = <Tea />
      state = 'Having A Tea'
      break
    case 18 <= hour && hour <= 22:
      emoji = <Working />
      state = 'Working'
      break
    case 22 <= hour && hour <= 24:
      emoji = <WatchingMovie />
      state = 'Watching A Movie'
      break
  }
  return (
    <Container>
      <div className="home-intro">
        <div className="home-intro_left">
          {/* <div className="home-intro_title">
                        <Logo/>
                        <h1>Portfolio</h1>
                    </div> */}
          <div className="home-intro_main_container">
            <div className="home-intro_introduce">
              <h1>HI, I'M MOSTAFA</h1>
              <br />
              <h1>
                <ReactTypingEffect
                  eraseDelay={eraseDelay}
                  typingDelay={typingDelay}
                  speed={speed}
                  eraseSpeed={speed}
                  text={['WEB DEVELOPER', '3D MODELER', 'AI PROGRAMMER']}
                />
              </h1>
            </div>
            <div className="home-intro_main">
              <p>
                I'm a <strong>{age}</strong> years old computer engineer and
                creative developer, my job is creating 3D experiences for my
                client's website.
              </p>
              <p>
                i've got experience with <strong>Programming</strong> and{' '}
                <strong>3D Modeling</strong> during past{' '}
                <strong>{years}</strong> years.
              </p>
            </div>
            <div className="home-intro_state">
              <Programmer />
              <p>
                I'm probably <strong>{state}</strong>
              </p>
              {emoji}
            </div>
            <ul className="home-intro-social">
              <li>
                <a href="https://github.com/Moizadloo">
                  <Github />
                </a>
              </li>
              <li>
                <a href="https://www.linkedin.com/in/mostafa-izadloo-2812111a2/">
                  <LinkedIn />
                </a>
              </li>
              <li>
                <a href="https://dribbble.com/Mostafa-Izadloo">
                  <Dribble />
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div className="home-intro_right">
          <div className="home-intro-me">
            <SCIntro />
          </div>
        </div>
      </div>
    </Container>
  )
}

export default Intro
